<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>

<script>
  import StyleEditor from './components/StyleEditor'
  import ResumeEditor from './components/ResumeEditor'
  import './assets/reset.css'

  export default {
    name: 'app',
    components: {
      StyleEditor,
      ResumeEditor
    },
    data() {
      return {
        interval: 40,
        currentStyle: '',
        enableHtml: false,
        fullStyle: [
          `/*
* Hi, my name is HairuoLiu.
* It is already December
* I graduated in an instant
* and I need to write a resume!
*/

/* First add transitions to all the elements */
* {
  transition: all .3s;
}
/* 
* The white background is too monotonous
* I need to change the background color! 
*/
html {
  color: #dedede; 
  background: #1E88E5;
}
/* The text is too close to the border */
.styleEditor {
    padding: .5em;
    border: 3px solid;
    overflow: auto;
    height: 95vh;
    width: 45vw; 
    margin: .5em;
    border-radius: 5px;
    border-color:#272822; 
    background: #272822;
    box-shadow: inset 0 0 0 0.5px rgba(0, 0, 0, 0.3);
    font-family: initial; 
}

/* Highlighting the code  */
.token.comment { 
  color: #857F6B; 
  font-style: italic; 
  }
.token.selector{ color: #a6e22e; }
.token.property{ color: #f92672; }
.token.punctuation{ color: #66d9ef; }
.token.function{ color: #ae81ff; }

/* add some 3D effects! */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed; left: 0; top: 0;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(10deg) translateZ(-100px) ;
          transform: rotateY(10deg) translateZ(-100px) ;
}

/* Now I need a editor */
.resume-div{
  position: fixed; right: 0; top: 0;
  padding: .5em;  margin: .5em;
  width: 50vw; height: 95vh;
  border: 3px solid;
  border-radius: 5px;
  background: white; color: #222;
  overflow: auto;
}
/* Well, I can start writing my resumes */


`,
          `
/*
 * Oh, The resume is Markdown type
 * I need to make it friendly to HR
 */
`
          ,
          `
/* Let's add some HTML style*/
.resume-div{
  padding: 2em;
}
.resume-div h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
.resume-div ul,.resume-div ol{
  list-style: none;
}
.resume-div ul> li::before{
  content: 'â€¢';
  margin-right: .5em;
}
.resume-div ol {
  counter-reset: section;
}
.resume-div ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resume-div blockquote {
  margin: 1em;
  padding: .5em;
  background: #ddd;
}
`],
        currentMarkdown: '',
        fullMarkdown: `
HairuoLiu [GitHub](https://github.com/HairuoLiu?tab=repositories)
----
 [SELECTED PROJECTS]('./ProjectPage.html')
----

EDUCATION
----
- The Ohio State University, Columbus, Ohio
- B.S. Computer Science Engineering, December 2017 
- Specialization: Information and Computation Assurance (CIA) 
- Overall GPA: 3.1; Achieved 3.5 past 2 terms

OBJECTIVE
----
Seeking to develop website,mobile applications,information security analysts in a full time position; Open to relocation.

QUALIFICATIONS
----
*Programming Language:  C, Java, JavaScript, C#, C++, Assembly language, Ruby, PHP

*App Development:  Android studio, Xcode, Visual studio, Amazon Alexa, Amazon Lambda, WeChat develop tools

*Web Development:  ASP.NET, Ruby on rails, Meteor, Middleman, Laravel, Vue, React, Bootstrap, materializecss

*Game Development:  Unity, Visual studio, Monogame

*Security tools:  CIS-CAT Pro, SANS Checklist, OSSEC, Kippo, nmap, Nessus, Metasploit, Tcpdump, wireshark

*Database:  MySQL, SQLite, Mongo DB, DynamoDB

INTERSHIP EXPERIENCE
----

1. Romp n' Roll Kindergarten China (China, Beijing) -- Summer, 2017 \n
  Full Stack Developer: Converted Photoshop designs to front-end code 
  Used ASP.NET to build website with mysql database. (http://preschool.rompy.cn)
2. Romp n' Roll China, Beijing -- Fall, 2016 \n
  Software Developer: Developed online booking application under WeChat platform.
3. Hylanda (China, Beijing) -- Summer, 2014 \n
  Programmer: Wrote arithmetic analyzing big data with C language in team.



> If you want to email me, Click [My Email Address](mailto:liu.3502@osu.edu).

`
      }
    },
    created() {
      this.makeResume()
    },

    methods: {
      makeResume: async function () {
        await this.progressivelyShowStyle(0)
        await this.progressivelyShowResume()
        await this.progressivelyShowStyle(1)
        await this.showHtml()
        await this.progressivelyShowStyle(2)
      },
      showHtml: function () {
        return new Promise((resolve, reject) => {
          this.enableHtml = true
          this.$nextTick(() => {
            this.$refs.resumeEditor.goTop()
          })
          resolve()
        })
      },
      progressivelyShowStyle(n) {
        return new Promise((resolve, reject) => {
          let interval = this.interval
          let showStyle = (async function () {
            let style = this.fullStyle[n]
            if (!style) { return }
            // è®¡ç®—å‰ n ä¸ª style çš„å­—ç¬¦æ€»æ•°
            let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
            let prefixLength = length - style.length
            if (this.currentStyle.length < length) {
              let l = this.currentStyle.length - prefixLength
              let char = style.substring(l, l + 2) || ' '
              this.currentStyle += char
              if (style.substring(l , l + 1) === '\n' && this.$refs.styleEditor) {
                this.$nextTick(() => {
                  this.$refs.styleEditor.goBottom()
                })
              }
              setTimeout(showStyle, interval)
            } else {
              resolve()
            }
          }).bind(this)
          showStyle()
        })
      },
      progressivelyShowResume() {
        return new Promise((resolve, reject) => {
          let length = this.fullMarkdown.length
          let interval = this.interval
          let showResume = () => {
            if (this.currentMarkdown.length < length) {
              this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 2)
              let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1]
              let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2]
              if (prevChar === '\n' && this.$refs.resumeEditor) {
                this.$nextTick(() => this.$refs.resumeEditor.goBottom())
              }
              setTimeout(showResume, interval)
            } else {
              resolve()
            }
          }
          showResume()
        })
      }
    }
  }

</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-height: 100vh; position: relative;
  }

  html {
    min-height: 100vh;
  }
  *{
    box-sizing: border-box;
  }

</style>



